
.el-upload__input {
    display: none !important;
}
.image-rocket{
  width: 100%;
  display: block;
  height:300px;
  background-color: #fff;
}
.image {
    width: 100%;
    display: block;
}
.text-content {
    color: black;
    margin: 20px 0;
    font-size: 12px;
    text-align:justify;
}
/*p {
    margin: 0 0 0 0 !important;
}*/
.mapping-p{
    word-wrap: normal;
    text-overflow: ellipsis;
    line-height: 39px;
    width: 100%;
    box-sizing: border-box;
    background-color: #eef1f6;
    border: 1px solid #dfe6ec;
    margin-right: 20px;
    padding: 0 20px;
}
.mapping-hr{
    margin-top: 0px;
    margin-bottom: 0px;
    background-color: #99A9BF;
}



