
body { padding-top: 70px;
}
#wrapper {
    position: absolute;
    top: 55px;
    bottom: 0;
    left: 5px;
    right: 5px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    box-shadow: 0 0 0 2px rgba(0, 0, 0, 0.04);
    overflow: hidden
}
#content {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: auto;
    margin-top: 51px;
    background-color: #F7F7FF;
    padding: 20px;
    left: 80px
}



