
#tools {
    margin-left: 80px;
}
#tools #breadcrumb {
    border-radius: 0;
    padding: 15px 20px 15px 20px;
    border-bottom: 1px solid #DFE7EC
}
#tools #breadcrumb .title {
    font-weight: 800;
    color: #1abc9c;
}
#tools .breadcrumb{
    /*// 2c3e50*/
    background-color: #f1f4f7;
}
#tools #toolbar {
    margin-top: -70px;
    margin-right: 10px;
    float: right;
    height: 50px;
    padding: 8px;
    border-bottom: 0
}
#tools .btn-group,
#tools .label,
#tools form {
    float: right;
    display: inline-block
}
#tools .btn-group .btn span.badge {
    background-color: #2c3e50;
    padding: 3px 5px;
    font-size: 0.7em;
    font-weight: 200
}
#tools .label {
    margin-right: 5px;
    margin-top: 6px;
    padding: 5px;
    height: 23px;
    font-size: 0.9em;
    font-weight: 200
}
#tools .btn-group .btn {
    color: white;
    background-color: #1abc9c;
    border-color: #17a689
}
.btn-success:hover,
#tools .btn-group .btn:hover,
.btn-success:focus,
#tools .btn-group .btn:focus,
.btn-success:active,
#tools .btn-group .btn:active,
.btn-success.active,
#tools .btn-group .active.btn,
.open>.btn-success.dropdown-toggle,
#tools .btn-group .open>.dropdown-toggle.btn {
    color: white;
    background-color: #148f77;
    border-color: #0f705d
}




