
.el-table .cell {
    white-space: normal;
    /*word-break: break-all;*/
    line-height: 24px;
    overflow:hidden !important;
    white-space:nowrap !important;
    text-overflow:ellipsis !important;
}
.panel-pad {
    padding-right: 40px;
}
.widget-left {
    border-bottom-left-radius: 4px;
    border-top-left-radius: 4px;
    height: 80px;
    padding-top: 15px;
    text-align: center;
}
.widget-right {
    background: #FFFFFF none repeat scroll 0 0;
    border-bottom-right-radius: 4px;
    border-top-right-radius: 4px;
    color: #F8F8F8;
    font-weight: 300;
    height: 80px;
    line-height: 1.6em;
    margin: 0;
    padding: 20px;
    text-align: left;
}
.widget-right .text-muted {
    color: #9fadbb;
}
.widget-right .large {
    color: #5f6468;
    font-size: 2em;
}
.panel-pink .widget-left {
    background: #875EA2 none repeat scroll 0 0;
    color: #fff;
}
.panel-yellow .widget-left {
    background: #DC69AA none repeat scroll 0 0;
    color: #fff;
}
.panel-blue .widget-left {
    background: #58B7FF none repeat scroll 0 0;
    color: #fff;
}
.panel-teal .widget-left {
    background: #1ebfae none repeat scroll 0 0;
    color: #fff;
}
.panel-orange .widget-left {
    background: #F7BA2A none repeat scroll 0 0;
    color: #fff;
}
.panel-red .widget-left {
    background: #475669 none repeat scroll 0 0;
    color: #fff;
}
.panel-widget {
    background: #fff none repeat scroll 0 0;
}
.overview-panel-widget {
    background: #F7F7FF none repeat scroll 0 0;
}
.panel .tabs {
    margin: 0;
    padding: 0;
}
.demo-table-expand {
    font-size: 0;
}
.demo-table-expand label {
    width: 90px;
    color: #99a9bf;
}
.demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
}
.overview-p{
    word-wrap: normal;
    text-overflow: ellipsis;
    line-height: 39px;
    width: 100%;
    box-sizing: border-box;
    background-color: #eef1f6;
    border: 1px solid #dfe6ec;
    margin-right: 20px;
    padding: 0 20px;
}



